@font-face {
	font-family: "SourceSansProLight";
	src: url("../fonts/SourceSansProLight/SourceSansPro-Light.eot?#iefix")format("embedded-opentype"),
			url("../fonts/SourceSansProLight/SourceSansPro-Light.woff2") format("woff2"),
		url("../fonts/SourceSansProLight/SourceSansPro-Light.woff") format("woff"),
		url("../fonts/SourceSansProLight/SourceSansPro-Light.ttf") format("truetype");
	font-style: normal;
	font-weight: 100;
	font-display: fallback;
}

